.not-found {
  -ms-flex-align:  center;
      align-items:  center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  height: calc(100vh - 70px);
}

.not-found p {
  font-size: 10vw;
  margin: 0 0 50px 0;
  text-transform: uppercase;
}

.not-found a {
  background: #A8ACC7;
  border-radius: 2px;
  color: #FFF;
  letter-spacing: 0.125em;
  padding: 20px;
  text-align: center;
  text-transform: uppercase;
  width: 100vw;
}

@media(min-width: 600px) {
  .not-found a {
    width: 15vw;
  }
}


